.section {
  width: 80%;
  color: gray;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
  ul {
    margin: 2em 0;
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    li {
      list-style: none;
      flex-basis: 100%;
      padding: 0.8rem;
      border: 1px solid #ccc;
      border-radius: 5px;
      margin-bottom: 2%;
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;
      opacity: 0;
      animation: build 350ms ease-in-out forwards;
      @keyframes build {
        0% {
          opacity: 0;
          transform: translateY(-5px);
        }
        100% {
          opacity: 1;
        }
      }
      div {
        display: flex;
        button,
        a {
          display: block;
          all: unset;
          width: 2.4rem;
          padding: 0.8rem;
          cursor: pointer;
          &:hover {
            color: rgba(46, 204, 113, 1);
          }
        }
      }
    }
  }
}

.nothingToShow {
  text-align: center;
  font-size: 1.6rem;
  color: gray;
  margin: auto;
}
